// Map Projections
import {
  π, π_2, τ, π_2xdegrees,
  degrees, radians,
  abs, sign, zero, epsilon,
  sin, cos, tan, asin,
  log, sqrt,
  range
} from './sqMath';
// https://github.com/d3/d3-geo

export default class sqMapProjections {
  constructor() {
    this.frac = 0;   // fraction 0..1 to interpolate between projections
    this.pjt_list = [
      {name: 'Equirectangular',              fn: this.equirectangular},
      {name: 'Orthographic',                 fn: this.orthographic},
      {name: 'Nicolosi Globular Projection', fn: this.nicolosi_globular},
      {name: 'Kavrayskiy VII Projection',    fn: this.kavrayskiy_VII},
      {name: 'Mercator Projection',          fn: this.mercator},
      {name: 'Equal Earth Projection',       fn: this.equal_earth},
    ];
    this.ctr  = [0, 0];
    this.cull = true;   // backface culling on by default
  }

  projection = pt => this.equirectangular(pt);

  get_culling = () => this.cull;
  set_culling = (cull) => this.cull = cull;

  get_num_projections = () => this.pjt_list.length;

  normalize_pt = pt => {
    let lg = pt[0] - this.ctr[0];
    let lt = -pt[1];// + this.ctr[1];
    const lgM = 180;
    if      (lg < -lgM) lg += lgM*2;
    else if (lg >  lgM) lg -= lgM*2;
    /*
    if      (lg < -lgM) lg = -lgM;
    else if (lg >  lgM) lg =  lgM;
    */
    return [lg, lt];
  }

  is_orthographic = () => (this.projection === this.orthographic); 

  get_orthographic_radius = () => π_2xdegrees;

  get_range = () => [-180, -90, 180, 90];

  get_border = () => [-180+this.ctr[0], -90, 180+this.ctr[0], 90];

  get_projection_object = (id=null) => {
    let fn = (id !== null) ? this.pjt_list[id % this.pjt_list.length].fn : this.projection;
    return this.pjt_list.find(el => el.fn === fn);
  }
  
  set_tween = () => {
    this.projection_last = this.projection;
    const prj = (this.projection === this.orthographic) ?  this.mercator : this.projection;
    this.projection = pt => this.tween_pjt(prj, this.orthographic)(pt);
    /*
    this.projection = (this.projection_last === this.orthographic) ?
       pt => this.tween_pjt(this.orthographic, this.mercator) :
       pt => this.tween_pjt(this.projection_last, this.orthographic);
    */
  }

  end_tween = () => {
    this.projection = this.projection_last;
  }

  set_projection = id => {
    let obj = this.get_projection_object(id);
    if (obj) {
      this.projection = obj.fn;         // set a new projection function
    }
  }

  get_projection_name = id => {
    let obj = this.get_projection_object(id);
    return obj ? obj.name : "N/A";
  }

  set_location = (lg, lt) => { this.ctr = [lg, lt]; }
  
  // interpolate between two projections
  tween_pjt = (pjt1, pjt2) => pt => {
    const p1 = pjt1(pt);
    const p2 = pjt2(pt);
    const lerp_p = i => zero(p1[i]-p2[i]) ? p1[i] : (1 - this.frac) * p1[i] + this.frac * p2[i];
    const cull = (this.frac >= .99) ? p2[2] : null;
    return [lerp_p(0), lerp_p(1), cull];
  }

  interpolate_frac = frac => {
    this.frac = frac;
  }

  // ## Equirectangular projection
  equirectangular = pt => this.normalize_pt(pt);

  // map projection  https://en.wikipedia.org/wiki/List_of_map_projections
  // ## Mercator cylindrical, conformal projection
  // x = R(λ - λ0), y = R ln[tan(π/4 + φ/2)]
  mercator = pt => {
    const [lg, lt] = this.normalize_pt(pt);
    const threshold = 85; // 85.051129;
    const φ = range(lt, -threshold, threshold) * radians;
    return [ lg, log(tan(π/4 + φ/2)) * degrees ];
  }

  // ## The Equal Earth pseudocylindrical (equal-area) map projection
  // ## by Bojan Šavrič, Bernhard Jenny, and Tom Patterson (2018) inspired by Robinson projection.
  // ## https://www.researchgate.net/publication/326879978_The_Equal_Earth_map_projection
  equal_earth = pt => {  // longitude(λ), latitude
    const [lg, lt] = this.normalize_pt(pt); // keep degree unit since the result will be converted back to degree
    const φ   = lt * radians;
    const A1  = 1.340264;
    const A2  = -.081106;
    const A3  = .000893;
    const A4  = .003796;
    const θ   = asin( (sqrt(3) / 2) * sin(φ) );
    const θ_2 = θ * θ;
    const θ_6 = θ_2 ** 3;

    return [ 2 * sqrt(3)*lg*cos(θ) / (3*(A1 + 3*A2*θ_2 + θ_6*(7*A3 + 9*A4*θ_2))),
             θ*( A1 + A2*θ_2 + θ_6*(A3 + A4*θ_2) ) * degrees];
  }

  // ## Nicolosi globular projection, polyconic
  nicolosi_globular = pt => {
    const [lg, lt] = this.normalize_pt(pt);
    const λ = lg * radians;
    const φ = lt * radians;
    const π_2λ  = π/(2*λ);
    const sinφ = sin(φ);
    const cosφ = cos(φ);

    // https://beta.observablehq.com/@toja/nicolosi-globular-projection
    if (λ === 0 || abs(φ) === π_2) return [0, φ * degrees];
    else if (φ === 0)              return [λ * degrees, 0];
    else if (abs(λ) === π_2)       return [λ*cosφ * degrees, sinφ * π_2xdegrees];

    const b       = π_2λ - (1/π_2λ);
    const c       = 2*φ/π;
    const d       = (1 - c*c) / (sinφ - c);
    const sgnλ    = sign(λ);
    const b2      = b*b;
    const d2      = d*d;
    const b2_d2   = b2/d2;
    const d2_b2   = d2/b2;
    const b2_d2a1 = b2_d2 + 1;
    const d2_b2a1 = d2_b2 + 1;
    const M = (b *sinφ/d  - b/2) / b2_d2a1;
    const N = (d2*sinφ/b2 + d/2) / d2_b2a1;
    const n = N*N - (d2*sinφ*sinφ/b2 + d*sinφ - 1)/d2_b2a1;

    return [ (M + sqrt( M*M + cosφ*cosφ/b2_d2a1 )  * sgnλ) * π_2xdegrees,
             (N + sqrt( n < 0 ? 0 : n ) * sign(-φ*b) * sgnλ) * π_2xdegrees ];
  }

  // Kavrayskiy VII pseudocylindrical, compromise projection
  kavrayskiy_VII = pt => {
    const [lg, lt] = this.normalize_pt(pt);
    const λ = lg * radians;
    const φ = lt * radians;

    return [ 3*λ/τ * sqrt(π*π/3 - φ*φ) * degrees, lt ];
  }

  // https://en.wikipedia.org/wiki/Orthographic_projection_in_cartography
  orthographic = pt => {
    //const outer = (p >= -180 && p < -90) || (p > 90 && p <= 180);
    //const outer = (p < -90) || (p > 90);
    const [lg, lt] = this.normalize_pt(pt);
    const Δλ = lg * radians;
    const φ  = lt * radians;
    const φ0 = -this.ctr[1] * radians;
    const cosφ  = cos(φ);
    const cosφ0 = cos(φ0);
    const sinφ  = sin(φ);
    const sinφ0 = sin(φ0);
    const cosΔλ = cos(Δλ);
    // dot product with the normal (to the screen) =
    // (cosφ0cosλ0, cosφ0sinλ0, sinφ0)・(cosφcosλ, cosφsinλ, sinφ)
    // sinφ0 * sinφ + cosφ0 * cosφ * cosΔλ
    const cull = this.cull ? ( (sinφ0 * sinφ + cosφ0 * cosφ * cosΔλ < 0) ? true : null ) : null;
    return [ epsilon(  cosφ  * sin(Δλ) * π_2xdegrees ),
             epsilon( (cosφ0 * sinφ - sinφ0 * cosφ * cosΔλ) * π_2xdegrees ), cull];
  }
}  // class sqMapProjections

