// https://developer.mozilla.org/en-US/docs/Web/API/File/Using_files_from_web_applications#Example_Using_
// https://developers.google.com/web/updates/2012/07/Drag-and-drop-a-folder-onto-Chrome-now-available
import React from 'react';
import './DropArea.scss';

const CLASS_DROP       = "drop-area";
const CLASS_DROP_UNDER = "drop-gallery"

const lZ = n => (n < 10) ? '0' + n : n.toString();
const sec2hms = sec => {
  if (sec < 60) {
    return sec.toFixed(1) + 's';
  }
  let hstr = '';
  let s = Math.trunc(sec);
  const h = Math.trunc(s/3600);
  if (h > 0) {
    s -= h*3600;
    hstr = h + ':';
  }
  const m = Math.trunc(s/60);
  s -= m*60;
  return hstr + lZ(m) + ':' + lZ(s);
}
// const callifdef = (f, arg) => if (f && typeof f === "function") f(arg);
//
// ignore unnecessary escape character: \/
// eslint-disable-next-line
const getFilenameFromUrl = url => url.replace(/^.*[\\\/]/, '').split('.')[0];  // extract filename only

export const DropArea = props => {
  console.log("DropArea::render");
  const [state, setState] = React.useState([]);  // array of DOM elements <img> or <div>
  const refDA     = React.useRef(null); // DropArea
  const rFilelist = React.useRef([]);

  React.useEffect(() => {  // Prevent background <body> reacting to drop actions
    ['dragenter', 'dragover', 'dragleave', 'drop'].forEach(
      evName => document.body.addEventListener(evName, ev => ev.preventDefault(), false));
  }, []);

  React.useEffect(() => {
    let countAdded = 0;
    if (props.urls) {
      props.urls.forEach(url => {
        countAdded += RenderUrl(url);  // handleDropFileInfo or RenderImage or RenderVideo
      })
    }
    if (countAdded > 0)
      RenderUpdates();
// eslint-disable-next-line
  }, [props.urls]);  // exhaustive-deps warning

  const redundantFilename = fn => {
    const fl = rFilelist.current;
    for (let i=0; i < fl.length; i++) {
      if (fl[i].name === fn)
        return true;
    }
    return false;
  }

  // This event happens repeatedly!
  const dragDropCommon = (ev, entering) => {
    ev.preventDefault();
    ev.stopPropagation();
    //setState({ ...state, hilight: entering ? 'hilight' : null});
    refDA.current.classList.toggle('hilight', entering); // or .className = entering ? 'hilight' : '';
  }

  const handleDragStart = (ev, file) => {
    ev.dataTransfer.dropEffect = 'copy';
    ev.dataTransfer.setData("text/json", JSON.stringify(file));  // cannot pass object directly
  }

  const handleDragOver  = ev => {
    dragDropCommon(ev, true);
    ev.dataTransfer.dropEffect = 'copy';
  }

  const handleDragEnd   = ev => { dragDropCommon(ev, false); }
  const handleDragEnter = ev => { dragDropCommon(ev, true); }
  const handleDragLeave = ev => { dragDropCommon(ev, false); }
  // https://developer.mozilla.org/en-US/docs/Web/API/File/Using_files_from_web_applications
  const handleDrop = ev => {
    dragDropCommon(ev, false);
    /*
    const len = ev.dataTransfer.items.length;
    for (let i=0; i < len; i++) {
      let entry = ev.dataTransfer.items[i].webkitGetAsEntry();
      if (entry.isFile) {
        console.log(entry, "file");
      } else if (entry.isDirectory) {
        console.log(entry, "directory");
      }
    }
    */
    const dt = ev.dataTransfer;
    let countAdded = 0;
    for (let i=0; i < dt.items.length; i++) {
      const it = dt.items[i];
      if (it.kind === 'string') {
        console.log("handleDrop::", it.type, dt.getData(it.type));
        if (it.type.match('^text/plain')) {            // it is the target node
          it.getAsString(s => console.log(it.type, s));
        } else if (it.type.match('^text/json')) {      // application defined user data
          const fi = JSON.parse(dt.getData(it.type));
          handleDropFileInfo(fi);  // get the string set by dt.setData()
        } else if (it.type.match('^text/html')) {      // it is HTML
          const html = dt.getData(it.type);
          console.log(html);
        } else if (it.type.match('^text/uri-list')) {  // it is URI
          //it.getAsString(s => console.log(it.type, s));
          const url = dt.getData(it.type);
          console.log("handleDrop::", it.type, url);
          if (url.includes('app')) {
            let id = getFilenameFromUrl(url);
            if (id && id.match('^app')) {  // id: app01 ...
              handleDropFileInfo({type: 'app', url, id});
            }
          } else if (url.includes('youtube.com')) {
            let id = url.split('=')[1];
            if (id && id.length === 11) {
              handleDropFileInfo({type: 'youtube', url, id});
            }
          } else {
            countAdded += RenderUrl(url);  // handleDropFileInfo or RenderVideo
          }
        }
      } else if (it.kind === 'file') {
        //console.log(it);
        if (it.type.match('^image/')) {
          countAdded += RenderImg(it.getAsFile());
        } else if (it.type.match('^video/')) {
          countAdded += RenderVideo(it.getAsFile());
        } else if (it.kind === '') {
        } else {
          console.log(it.kind, "not supported yet!");
        }
      }
    }
    if (countAdded > 0)
      RenderUpdates();
  }

  const handleDropFileInfo = fInfo => props.onDropFileInfo && props.onDropFileInfo(fInfo);

  const RenderUpdates = () => {
    props.onDrop && props.onDrop(rFilelist.current);
    setState([...state]);  // state should be an array, not an object; setState(state) will not re-render
  }

  const handleFiles = files => {  // called by input change event directly
    if (!files.length) {
      console.log("No files selected!");
      return;
    }

    let countAdded = 0;
    for (let i = 0; i < files.length; i++) {  // files is an object, cannot apply forEach
      countAdded += HandleFile(files[i]);
    }
    if (countAdded > 0)
      RenderUpdates();
  }

  const updateFilelist = file => {
    if (redundantFilename(file.name)) {
      console.log(file.name, "already exists.");
      return -1;
    }
    const fl = rFilelist.current;
    fl.push({
      type : file.type,
      name : file.name,
      size : file.size,
      //date : file.lastModifiedDate,
      url  : window.URL.createObjectURL(file),  // save to release later
      id   : file.type.slice(0,2) + fl.length,  // 'vi' or 'im' plus index (starts from 0)
    });
    return fl.length - 1;
  }

  const RenderImg = file => {
    const id = updateFilelist(file);
    if (id < 0)
      return 0;
    const fi = rFilelist.current[id];  // file info
    state.push(<img key={id} id={fi.id} src={fi.url} alt={fi.name}
                 draggable onDragStart={ev => handleDragStart(ev, fi)}/>);
    return 1;
  }

  const handleLoadedMetadata = ev => {
    const tg = ev.target;
    const ds = tg.parentElement.dataset;  // parent div.dataset
    ds.duration  = sec2hms(tg.duration);
    ds.width     = tg.videoWidth;
    ds.height    = tg.videoHeight;
  }

  const RenderVideo = file => {
    const id = updateFilelist(file);
    if (id < 0)
      return 0;
    const fi = rFilelist.current[id];
    // Media Fragments URI (url + "#t=10") or video.currentTime = 10;
    // example, blob:http://localhost:3000/50c82a58-b01c-4450-b161-da006d44a152#t=10
    // Videos that are not seekable should not be set currentTime (fast-forwarded)!
    //video.currentTime = 5;
    //video.playbackRate = 2;  // autoPlay
    state.push(<div key={id} data-title={fi.name}
                    draggable onDragStart={ev => handleDragStart(ev, fi)}>
        <video id={fi.id}
          loop controls playsInline
          onLoadedMetadata={handleLoadedMetadata}
        >
          <source src={fi.url} type={fi.type} />
        </video>
      </div>);
    return 1;
  }

  const RenderUrl = url => {  // file info
    let img = false;
    let ext = null;
    if      (url.includes('.jpg'))  ext = 'jpg';
    else if (url.includes('.png'))  ext = 'png';
    else if (url.includes('.gif'))  ext = 'gif';
    else if (url.includes('.jpeg')) ext = 'jpeg';
    else if (url.includes('.bmp'))  ext = 'bmp';
    if (ext)
      img = true;
    else {
      if      (url.includes('.mp4'))  ext = 'mp4';
      else if (url.includes('.webm')) ext = 'webm';
    }
    if (!ext)
      return 0;
    const fn = getFilenameFromUrl(url);
    if (!fn)
      return 0;
    const name = fn + '.' + ext;
    if (redundantFilename(name)) {
      console.log(name, "already exists.");
      return 0;
    }
    const fl = rFilelist.current;
    const id = fl.length;
    const type = (img ? 'image' : 'video') + '/' + ext;
    const fi = {
      type,
      name,
      size: 0,  // unknown
      url,
      id: type.slice(0,2) + id,  // 'vi' or 'im' plus index (starts from 0)
    };
    if (props.onDropFileInfo)  {
      props.onDropFileInfo(fi);
      return 0;  // no render on the DropArea
    }
    fl.push(fi);
    if (img) {
      state.push(<img key={id} id={fi.id} src={fi.url} alt={fi.name}
                      draggable onDragStart={ev => handleDragStart(ev, fi)}/>);
    } else {
      // Media Fragments URI (url + "#t=10") or video.currentTime = 10;
      // example, blob:http://localhost:3000/50c82a58-b01c-4450-b161-da006d44a152#t=10
      // Videos that are not seekable should not be set currentTime (fast-forwarded)!
      //video.currentTime = 5;
      //video.playbackRate = 2;  // autoPlay
      state.push(<div key={id} data-title={fi.name}
                  draggable onDragStart={ev => handleDragStart(ev, fi)}>
          <video id={fi.id}
            loop controls playsInline
            onLoadedMetadata={handleLoadedMetadata}
          >
            <source src={fi.url} type={fi.type} />
          </video>
        </div>);
    }
    return 1;
  }

  const HandleFile = file => {  // FileList returned by the files property of <input> element
    // https://cloudinary.com/documentation/upload_images#uploading_with_a_direct_call_to_the_api
    // PW: 536Forest!@#
    if (file.type.startsWith('image/')) {
      return RenderImg(file);
    } else if (file.type.startsWith('video/')) {
      return RenderVideo(file);
    } else {
      console.log(file.name, file.type, "not supported yet!");
      return 0;
    }
    /* Upload files
    const url = 'https://api.cloudinary.com/v1_1/dicvu5udo/image/upload';
    const formData = new FormData();
    formData.append('upload_preset', 'hkbexlp4');
    formData.append('file', file);
    fetch(url, { method: 'POST', body: formData })
      .then(() => console.log("Data uploaded"))
      .catch(() => console.log("Upload error!"));
    */
    /*
    const xhr = new XMLHttpRequest();
    xhr.open('POST', url, true);
    xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');

    xhr.addEventListener('readystatechange', function(ev) {
      if (xhr.readyState === 4) {
        if (xhr.status === 200)
          updateProgress(i, 100);
        // Error. inform the user
      }
    });
    xhr.send(formData);
    */
  }

  const handleClearClick = () => {
    console.log(rFilelist.current.length, "files");
    setState([]);
    rFilelist.current = [];
    /*
    const gal = refGl.current;
    while (gal.firstChild) {  // NEVER use gal.innerHTML = ""
      const node = gal.firstChild;
      if (node.url) {
        console.log("revokeObjectURL", node.url);
        window.URL.revokeObjectURL(node.url);
      }
      gal.removeChild(node);
    }
    */
  }

  const DropForm = () =>
    <form className="da-form">
      <input
        id="df-file"
        type="file"
        multiple accept="image/*,video/*,audio/*,.pdf"
        onChange={ev => handleFiles(ev.target.files)}
        className="vis-hid"
      />
      <label className="df-label" htmlFor="df-file">Select or Drop Files</label>
    </form>;

  // onchange attribute of <input>
  // ev.target:        <input ...>
  // ev.target.value : filename
  // ev.target.files : files
  // Using a label element to trigger a hidden file input element
  return (
    <div
      className  ={CLASS_DROP}
      ref        ={refDA}
      onDragEnter={handleDragEnter}
      onDragOver ={handleDragOver}
      onDragLeave={handleDragLeave}
      onDrop     ={handleDrop}
      //onDragStart={handleDragStart}
      onDragEnd  ={handleDragEnd}
      style      ={props.style}
    >
      {props.children}
      {props.form && <DropForm />}
      {!props.noUi &&
        <div className={CLASS_DROP_UNDER}>
            {(props.help && state.length <= 6) &&
            <p className="drop-help">{props.help}</p>}
          <button type="button" className="btn-clear" onClick={handleClearClick}>
            <span role="img" aria-label="clear">❌</span>
          </button>
          {state}
        </div>}
      {props.list && <ListViewFiles filelist={rFilelist.current} />}
    </div>
  );
}

const ListViewFiles = props => {  // props.list is an array of file info
  const { filelist } = props;
  const [fold, setFold] = React.useState(false);  // true: fold, false: expanded
  if (filelist.length === 0)
    return null;
  let i=0;

  const fmtFilesize = sz => {
    if (sz < 1024*1024) {
      return (sz/1024).toFixed(1) + 'KB';
    }
    return (sz/(1024*1024)).toFixed(1) + 'MB';
  }

  return (
    <div>
      <button className="drop-list-btn" type="button"
        onClick={()=>setFold(!fold)}
      >Fileslist</button>
      {!fold &&
      <ul className="drop-list">
        {filelist.map(f =>
          <li key={i++}>
            <span>{`[${f.type}] `}</span>
            <a href={f.url} target="_blank" rel="noopener noreferrer">{f.name}</a>
            <span>{`(${fmtFilesize(f.size)})`}</span>
          </li>
        )}
      </ul>}
    </div>
  );
}
