const pjt = new sqMapProjections();

const map_size = bbox => bbox ?
  Math.min(bbox[2] - bbox[0],
           bbox[3] - bbox[1]) : 360;
//const get_bbox = bbox => bbox ? bbox : [-180, -90, 180, 90];

const bbox2viewBox = bbox => {
  if (!bbox)
    return '-180 -90 360 180';
  const vF = v => v.toFixed(2);
  return `${vF(bbox[0])} ${vF(bbox[1])} ${vF(bbox[2] - bbox[0])} ${vF(bbox[3] - bbox[1])}`;
}

// Compact svg generator that checks if horizontal or vertical lines and uses relative coordinates
// returns '' when no change in positions
const svg_line = (p1, p2) => {
  const s = v => Number.isInteger(v) ? v : Number.parseFloat(v.toFixed(3));  // short number
  if (p1 === null)
    return 'M' + s(p2[0]) + ',' + s(p2[1]);  // starting point
  const dx = p2[0] - p1[0];
  const dy = p2[1] - p1[1];
  if (zero(dx))  return (zero(dy)) ? '' : 'v' + s(dy);
  if (zero(dy))  return (zero(dx)) ? '' : 'h' + s(dx);
  return 'l' + s(dx) + ',' + s(dy);  // delta encode
}

const Topo2svg = props => {
  const {mp, bCountries, ...other} = props;  // extract mp (map) only, and pass the other props for SVG

  if (mp.type !== 'Topology') {
    console.log('Invalid map: should be a topojson file.');
    return null;
  }
  //const t0 = performance.now();

  // initialize per map
  const scale = mp.transform ? mp.transform.scale     : [1, 1];
  const tranz = mp.transform ? mp.transform.translate : [0, 0];
  const keyName = bCountries ? "countries" : "land";
  let color_num = 0;
  let fColor = ['Cornsilk', 'BlanchedAlmond', 'Bisque', 'NavajoWhite', 'Wheat', 'Burlywood', 'Tan', 'RosyBrown',
                'SandyBrown', 'Goldenrod', 'DarkGoldenrod', 'Peru', 'Chocolate',
                'SaddleBrown', 'Sienna', 'Brown', 'Maroon'];
  let lns = [];
  let k = 0;
  //let countAll=0;  // debug
  //let countOpt=0;
  //let maxΔφ = 0;

  // stitch multiple arcs
  const stitch_arcs = (l, bClosed=true) => {
    // arc to an array of points
    const arc2pts = (ac_id, bRemoveLast=true) => {
      // https://github.com/topojson/topojson-specification
      const decode_arc = arc => {
        let p = [0, 0];
        return arc.map(pos => {
          pos = pos.slice();
          pos[0] = (p[0] += pos[0]) * scale[0] + tranz[0];  // rotate_π( 
          pos[1] = (p[1] += pos[1]) * scale[1] + tranz[1];
          return pos;
        });
      }
      const bReverse = (ac_id < 0);
      const pts = decode_arc(mp.arcs[bReverse ? ~ac_id : ac_id]);
      if (bReverse)
        pts.reverse();
     // countAll += pts.length;
      if (bRemoveLast)  // remove redundancy
        pts.splice(-1, 1);
      return pts;
    }

    let n_pts = 0;
    let d_p = '';
    let p_last = null;
    l.forEach(i =>
      arc2pts(i).forEach(pt => {
        const p = pjt.projection(pt);
        if (!p[2]) {
          d_p += svg_line(p_last, p);
          p_last = p;
          n_pts++;
        }
      })
    );
    if (n_pts < 2) {
      return null;  // culled or invalid polygons
    }
    //countOpt += n_pts;
    if (bClosed && (n_pts > 2)) {
      d_p += 'Z';  // close
    }
    return d_p;
  }

  const line_string2path = ls => {
    const d_p = stitch_arcs(ls, false);  // open path
    if (d_p)
      lns.push(<path key={k++} d={d_p} fill="none" />);
  }

  // use a closed <path> instead of <polygon>
  // inner polygons (counter-clockwise winding) follow after the first polygon
  // Outer & Inner loop drawings: http://bl.ocks.org/mapsam/c76ed3ac3a79d9736dd7
  // Most of data end with a single polygon.
  const polygon2path = pg => {
    let d_p = '';  // empty string
    pg.forEach(lr => {    // for each LinearRing
      const d = stitch_arcs(lr);
      if (d)
        d_p += d;
    });
    if (d_p) {  // need to set fill color only for bCountries
      if (bCountries)
        lns.push(<path key={k++} d={d_p} fill={fColor[color_num % fColor.length]} />);
      else
        lns.push(<path key={k++} d={d_p} />);  // set fill color in css
    }
  }

  const geometry2svg = obj => {
    if (!obj)
      return;
    switch (obj.type) {
      case 'Point'             :
      case 'MultiPoint'        :
      case 'LineString'        : line_string2path(obj.arcs);                      break;
      case 'MultiLineString'   : obj.arcs.forEach(ls => line_string2path(ls));    break;
      case 'Polygon'           : polygon2path(obj.arcs);                          break;
      case 'MultiPolygon'      : obj.arcs.forEach(pg => polygon2path(pg));        break;
      case 'GeometryCollection': obj.geometries.forEach(gm => geometry2svg(gm));  break;
      default:
    }
  /*
    if (geom.hasOwnProperty("properties") &&
        geom.properties.hasOwnProperty("SOV_A3") &&
        geom.properties.SOV_A3 === 'JPN')
      console.log(geom.properties);
  */

    // ISO 3166 country codes (249 countries)
    if (obj.hasOwnProperty("id")) {
      color_num = obj.id % fColor.length;
    }
  }

  // topo2svg
  for (let key in mp.objects) {  // to access the unknown key
    //if (mp.objects.hasOwnProperty(key)) {
      //console.log(mp.objects[key]);
      //console.log(mp.objects[key].type);
    if (key === keyName) {  // 'countries' or 'land'
      geometry2svg(mp.objects[key]);
    }
  }

  //console.log("Total points:", countAll, "Optimized:", countOpt,
  //  "Reduced:", `${((countAll-countOpt)/countAll).toFixed(2)}%`);
  // console.log("maxΔφ=", maxΔφ);
  //const t1 = performance.now();
  //console.log('topo2svg:', (t1-t0).toFixed(2), 'ms');

  return <g className={keyName} {...other}>{lns}</g>;
}

const MapBorder = props => {
  if (pjt.is_orthographic()) {
    return <circle className="map_border" cx={0} cy={0} r={pjt.get_orthographic_radius()} />;
  }
  const B = pjt.get_border();
  let pt = [B[0], B[1]];
  let p_last = null;
  let d_p = '';

  const move_pt = (i, sgn) => {
    let p = pjt.projection(pt);
    d_p += svg_line(p_last, p);
    p_last = p;  // use .slice() to clone the array
    pt[i] += sgn * props.step;      // should be a divisor of 90, otherwise out of range trouble can happen
  }

  while (pt[0] < B[2]) move_pt(0,  1);
  while (pt[1] < B[3]) move_pt(1,  1);
  while (pt[0] > B[0]) move_pt(0, -1);
  while (pt[1] > B[1]) move_pt(1, -1);
  d_p += 'Z';
  //console.log(d_p.length);

  return <path className="map_border" d={d_p} />;
}

