const svg_latitude = (y, step, x1, x2) => {
  let p_last = null;
  let d_p = '';
  for (let x = x1; x <= x2; x += step) {
    let p = pjt.projection([x, y]);
    if (!p[2]) {
      d_p += svg_line(p_last, p);
      p_last = p;
    } else {
      p_last = null;
    }
  }
  return d_p;
}

const Graticule = props => {
  //const t0 = performance.now();
  const B = pjt.get_range();
  let d_p = '';
  // Optimization needed: Build the mesh first and then convert to the svg data
  // Longitude
  for (let x = B[0]; x < B[2]; x += props.step) {
    const y_offset = (x % 45) ? 15 : 0;  // insert offset except for each 45° of the latitude
    let p_last = null;
    for (let y = B[1]+y_offset; y <= B[3]-y_offset; y += props.step) {
      let p = pjt.projection([x, y]);
      if (!p[2]) {
        d_p += svg_line(p_last, p);
        p_last = p;
      } else {
        p_last = null;
      }
    }
  }
  // Latitude
  for (let y = B[1] + props.step; y < B[3]; y += props.step)
    d_p += svg_latitude(y, props.step, B[0], B[2]);
  //const t1 = performance.now();
  //console.log('Graticule:', (t1-t0).toFixed(2), 'ms');

  return <path className="graticule" d={d_p} />;
}

// Tropic of Cancer, Tropic of Capricorn: ±23.4368° of the equator
const Tropics = props => {
  const B = pjt.get_range();
  let d_p = '';
  for (let y of props.lat)
    d_p += svg_latitude(y, props.step, B[0], B[2]);
  return <path className={props.className} d={d_p} />;
}

const SvgMain = props =>
  <svg width={props.width} height={props.height} viewBox={bbox2viewBox(world.bbox)} >
    <defs>
      <filter id='dropshadow' height="130%">
        <feGaussianBlur in="SourceAlpha" stdDeviation="1" />
        <feOffset dx="1" dy="1" result="offsetblur" />
        <feComponentTransfer>
          <feFuncA type="linear" slope="0.5" />
        </feComponentTransfer>
        <feMerge>
          <feMergeNode />
          <feMergeNode in="SourceGraphic" />
        </feMerge>
      </filter>
      <filter id="glow"
        x="-50%" y="-50%" width="200%" height="200%"
        colorInterpolationFilters="sRGB"
      >
        <feGaussianBlur result="coloredBlur" stdDeviation="4" />
        <feMerge>
          <feMergeNode in="coloredBlur" />
          <feMergeNode in="coloredBlur" />
          <feMergeNode in="coloredBlur" />
          <feMergeNode in="SourceGraphic" />
        </feMerge>
      </filter>
    </defs>
    <g filter="url(#glow)">
      <MapBorder step={5} />
      <Graticule step={15} />
      <Topo2svg mp={world} bCountries={props.bCountries}
        strokeWidth={(map_size(world.bbox)/1000)}
      />
      <Tropics className="tropics" lat={[-23.4368, 23.4368]} step={5} />
      <Tropics className="tropic0" lat={[0]} step={5} />
    </g>
  </svg>;

const GeoAniDemo = props => {
  const aniFrac = useAnimation('inOutCubic', 5000, 0);  // easing: linear, inQuad, inCubic, elastic, inExpo, ...

  pjt.set_tween();
  //pjt.set_culling(true);
  pjt.interpolate_frac(aniFrac);
  if (aniFrac === 1) {
    pjt.end_tween();
  }

  return (
    <div>
      <SvgMain bCountries={props.bCountries} width={props.width} height={props.height} />
    </div>
  );
}

export const GeoTest = props => {
  // Declare a new state variable, which we'll call "count"
  const [count,      setCount]        = useState(1);
  const [bCountries, toggleCountries] = useState(false);
  const [bCull,      toggleCull]      = useState(false);  // transparent
  const [bAni,       toggleAni]       = useState(false);
  const [height,     setHeight]       = useState(props.height ? props.height : 480);    // height
  //lg:139.6917+30, lt:35.6895   // Tokyo longitude, latitude of the projection center
  //lg:-118.4912-40, lt:34.0195  // Santa Monica, CA
  const [loc,        setLoc]          = useState({lg: 139.6917, lt: 35.6895}); 
  const [tranz,      setTranz]        = useState({dx: 0, dy: 0});
  const width = height; //Math.trunc(height * 800/480);
  const normalize = a => {  // let the |degree| be contained within 360°
    const s = v => Number.isInteger(v) ? v : Number.parseFloat(v.toFixed(3));  // short number
    const aM = 360;
    return s( (a < -aM) ? a + aM : ( (a > aM) ? a - aM : a ) );
  }

  const move_loc = (dx, dy) => {
    setLoc({
      lg: normalize(loc.lg - dx*.4),
      lt: normalize(loc.lt + dy*.4)
    });
  };

  pjt.set_location(loc.lg, loc.lt);
  pjt.set_projection(count);
  //pjt.set_location(loc.lg, loc.lt);
  pjt.set_culling(bCull);
  //pjt.set_location(loc.lg + aniFrac*(139.6917+30), loc.lt + aniFrac*35.6895);
/*
  useEffect(() => {  // run after every render!
    // Update the document title using the browser API
    document.title = pjt.get_projection_name();
  }, [count]);  // Only re-run the effect if count changes
  // <p className="report" >{pjt.get_projection_name()}: {loc.lg}°, {loc.lt}°</p>
*/

  const aniFrame = () => {
    if (bAni)
      return;
    setLoc({
      lg: normalize(loc.lg - 1.2),
      lt: loc.lt
    });
  }

  useAnimationFrame(aniFrame, 1000/8);

  const handleDoubleClick = ev => {
    ev.preventDefault();
    ev.stopPropagation();
    const x = Math.trunc(width/2);
    const y = Math.trunc(height/2);
    setTranz({dx: ev.clientX - x, dy: ev.clientY - y});
  }

  return (
    <EventMove
      render={move_loc}
      onDoubleClick={ev => handleDoubleClick(ev)}
    >
      <div className="geoTop"
        style={{transform: `translateX(${tranz.dx}px) translateY(${tranz.dy}px)`}}
      >
        {bAni ? <GeoAniDemo bCountries={bCountries} width={width} height={height} /> :
                <SvgMain    bCountries={bCountries} width={width} height={height} />}
      </div>
      <div className="btnCont">
        {!bAni && <button className="btnDemo" onClick={() => setCount(count + 1)}>switch projection</button>}
        <button className="btnDemo" onClick={() => toggleCountries(!bCountries)}>{bCountries ? 'Land'        : 'Countries'}</button>
        <button className="btnDemo" onClick={() => toggleCull(!bCull)}>          {bCull      ? 'Transparent' : 'Opaque'}</button>
        <button className="btnDemo" onClick={() => toggleAni(!bAni)}>            {bAni       ? 'Map'         : 'Animation'}</button>
        <button className="btnDemo" onClick={() => setHeight(height > 100 ? height-80 : height)}> {'-'}</button>
        <button className="btnDemo" onClick={() => setHeight(height+80)}>                {'+'}</button>
      </div>
    </EventMove>
  );
}
