import React from 'react';
import { makeStyles }  from '@material-ui/core/styles';
import AppBar          from '@material-ui/core/AppBar';
import CssBaseline     from '@material-ui/core/CssBaseline';
import Toolbar         from '@material-ui/core/Toolbar';
import IconButton      from '@material-ui/core/IconButton';
import Fab             from '@material-ui/core/Fab';  // Floating Action Buttons
import MenuIcon        from '@material-ui/icons/Menu';
import VideocamIcon    from '@material-ui/icons/Videocam';  // ./node_modules/@material-ui/icons/
import VideocamOffIcon from '@material-ui/icons/VideocamOff';
import PlayIcon        from '@material-ui/icons/PlayArrow';
import VolumeMuteIcon  from '@material-ui/icons/VolumeMute';
import SearchIcon      from '@material-ui/icons/Search';
import MoreIcon        from '@material-ui/icons/MoreVert';

const useStyles = makeStyles(theme => ({
  text: {
    padding: theme.spacing(2, 2, 0),
  },
  subheader: {
    backgroundColor: theme.palette.background.paper,
  },
  appBar: {
    top: 'auto',
    bottom: 0,
  },
  grow: {
    flexGrow: 1,
  },
  fabButton: {
    /*
    position: 'absolute',
    zIndex: 1,
    top: 2,
    left: 0,
    right: 0,
    margin: '0 auto',
    */
    margin: theme.spacing(1)
  },
}));

const FabToggleVideocam = props => {  // to localize (not to propagate) rendering
  const classes = useStyles();
  const [video, setVideo] = React.useState(false);
  const handleVideocam = () => {
    setVideo(!video);
    props.onVideo(!video);
    //console.log("videocam clicked");
  }

  return (
    <Fab size="small" color={video ? "default" : "secondary"} aria-label="Videocam" className={classes.fabButton}>
      {video ?
      <VideocamOffIcon onClick={()=>handleVideocam()} /> :
      <VideocamIcon    onClick={()=>handleVideocam()} />}
    </Fab>
  );
}

export default function BottomAppBar(props) {
  console.log("BottomAppBar::render");
  const classes = useStyles();

  return (
    <>
      <CssBaseline />
      <AppBar position="fixed" color="primary" className={classes.appBar}>
        <Toolbar>
          <IconButton edge="start" color="inherit" aria-label="Open drawer" onClick={props.onStart}>
            <MenuIcon />
          </IconButton>
          <FabToggleVideocam onVideo={props.onVideo} />
          <Fab size="small" color="default" aria-label="VolumeMute" className={classes.fabButton}>
            <VolumeMuteIcon onClick={()=>console.log("VolumeMute")} />
          </Fab>
          <Fab size="small" color="default" aria-label="Play" className={classes.fabButton}>
            <PlayIcon onClick={()=>console.log("Play")} />
          </Fab>
          <div className={classes.grow} />
          <IconButton color="inherit">
            <SearchIcon />
          </IconButton>
          <IconButton edge="end" color="inherit">
            <MoreIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
    </>
  );
}
