// used in './VideoEditor'
import React            from 'react';
import { EarthMoon3D }  from './demo/em/EarthMoon3D';
import { GeoTest }      from './demo/go/SQGeo';
import './App.scss';

export const EmbedYouTube = props =>
  <iframe
    title       = "YouTube"
    width       = {props.width}
    height      = {props.height}
    src         = {`https://www.youtube.com/embed/${props.vID}?autoplay=1&mute=1&enablejsapi=1`}
    frameBorder = "0"
    allow       = "accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
    allowFullScreen
  />;

export const Layers = props => {
  // La Vie en Rose: -NK9zdPj-os
  // Cafe de Flore: w02agoYJ_H8
  // New York Bar: lzQ3IS1Xq2s
  // Fly Me To The Moon w0ZnimhDDhs
  const { width, height, appId } = props;
  console.log("Layers::", appId);
  const AppCompo = () => {
    switch (appId) {
      case 'app01': return <GeoTest height={Math.trunc(height * 3/4)} />;
      case 'app02': return <EarthMoon3D width={width} height={height} />;
      default     : return null;
    }
  }

  return (
    <div className="ve-cont">
      {props.children}
      <div className="ve-l1">
        <AppCompo />
      </div>
    </div>
  );
}

