import React from 'react';

import { makeStyles }  from '@material-ui/core/styles';
import TextField       from '@material-ui/core/TextField';
import IconButton      from '@material-ui/core/IconButton';
import CKClearIcon     from '@material-ui/icons/Delete';
import ZoomInIcon      from '@material-ui/icons/ZoomIn';
import CKPickIcon      from '@material-ui/icons/Colorize';
import CKDropIcon      from '@material-ui/icons/InvertColorsOff';
import CKShotIcon      from '@material-ui/icons/Camera';
import Avatar          from '@material-ui/core/Avatar';
import Tooltip         from '@material-ui/core/Tooltip';
import Switch          from '@material-ui/core/Switch';
import Slider          from '@material-ui/core/Slider';
import Snackbar        from '@material-ui/core/Snackbar';
import CloseIcon       from '@material-ui/icons/Close';

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
  },
  textField: {
    //marginTop: theme.spacing(2),
    //marginLeft : theme.spacing(1),
    //marginRight: theme.spacing(1),
  },
  button: {
    height: 40,
    marginTop    : theme.spacing(1),
    marginBottom : theme.spacing(1),
    padding      : theme.spacing(1),
  },
  avatar: {
    marginTop   : theme.spacing(1),
    marginBottom: theme.spacing(1),
    marginRight : theme.spacing(1),
  },
  switch: {
    marginTop   : theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  slider: {
    width: 200,
    marginTop   : theme.spacing(2),
    marginLeft  : theme.spacing(1),
    marginBottom: theme.spacing(1),
    zIndex      : 100000,
  },
  close: {
    padding: theme.spacing(.5),
  },
}));

const ChromaKeyToolbox = props => {
  console.log("ChromaKeyToolbox::render");
  const { dispatch } = props;
  const cls = useStyles();
  const [on,    setOn]    = React.useState(true);  // switch on
  const [mode,  setMode]  = React.useState(1);     // 1: Pick Color 2: Snapshot
  const [snack, setSnack] = React.useState(null);  // Snackbar message

  const dispatchNewMode = newMode => {
    dispatch({type: 'setCKMode', payload: newMode});
  }
  const handleSwitch   = ev => {
    setOn(ev.target.checked);
    dispatchNewMode(ev.target.checked ? mode : 0);
  }
  const handlePickMode = () => {
    const newMode = mode === 1 ? 2 : 1;
    if (newMode === 2) {
      setSnack("Click Work Area to capture the frame");
    }
    setMode(newMode);
    dispatchNewMode(newMode);
  }
  const handleCKClear  = () => { setSnack("Chroma Keys cleared");      dispatch({type: 'clearCK'}); }
  const handleCKDrop   = () => { setSnack("Removed last Chroma Key");  dispatch({type: 'dropCK'}); }
  const handleCKThr    = (ev, newVal) => { dispatch({type: 'setCKThr', payload: newVal}); }
  const handleZoomIn   = () => { setSnack("Under construction"); }
  const handleSnackbarClose = (ev, reason) => setSnack(null);

  return (
    <>
    <Tooltip title="Chroma Key On/Off">
      <Switch
        checked={on}
        onChange={handleSwitch}
        className={cls.switch}
        value="switchCK"
        inputProps={{ 'aria-label': 'secondary checkbox' }}
      />
    </Tooltip>
    <Tooltip title="Pick Color / Snapshot">
      <IconButton
        className={cls.button}
        disabled={!on}
        aria-label="Toggle Pick Mode"
        onClick={handlePickMode}
      >
        {mode === 1 ?
          <CKPickIcon /> : 
          <CKShotIcon />}
      </IconButton>
    </Tooltip>
    <Tooltip title="Clear Chroma Keys">
      <IconButton
        className={cls.button}
        disabled={!on}
        aria-label="Clear" onClick={handleCKClear}>
        <CKClearIcon />
      </IconButton>
    </Tooltip>
    <Tooltip title="Undo Chroma Key">
      <IconButton
        className={cls.button}
        disabled={!on}
        aria-label="Color Off"
        onClick={handleCKDrop}>
        <CKDropIcon />
      </IconButton>
    </Tooltip>
    <Tooltip title="Color Distance Threshold">
      <Slider
        className={cls.slider}
        disabled={!on}
        defaultValue={70}
        valueLabelDisplay="auto"
        step={10}
        marks
        min={10}
        max={440}
        onChange={handleCKThr}
      />
    </Tooltip>
    <Tooltip title="Zoom In">
      <IconButton
        className={cls.button}
        aria-label="ZoomIn"
        onClick={handleZoomIn}>
        <ZoomInIcon />
      </IconButton>
    </Tooltip>
    <Snackbar
      className={cls.snack}
      anchorOrigin={{vertical: 'bottom', horizontal: 'right'}}
      open={snack !== null}
      autoHideDuration={5000}
      onClose={handleSnackbarClose}
      ContentProps={{'aria-describedby': 'msg-id'}}
      message={<span id="msg-id">{snack}</span>}
      action={[<IconButton
        key="close"
        aria-label="Close"
        color="inherit"
        className={cls.close}
        onClick={handleSnackbarClose}
        >
          <CloseIcon />
        </IconButton>
      ]}
    />
    </>
  );
}

const RandomAvatar = props => {
  console.log("RandomAvatar::render");
  const cls = useStyles();
  const [rUser, setRUser] = React.useState(null);
  // document.getElementById("outlined-text-input").value = '';  // to clear TextField

  const generateRandomUser = () => "https://randomuser.me/api/portraits/" +
              (Math.random() <= .5 ? 'wo' : '') + 'men/' + 
               Math.floor( Math.random() * Math.floor(99) ) + ".jpg";

  React.useEffect(() => {  // so that the random user avatar does not change for each render
    setRUser(generateRandomUser());
  }, []);

  const handleClick = () => {
    props.onClick && props.onClick();
    setRUser(generateRandomUser());
  }

  return (
    <Tooltip title="Webcam On">
      <Avatar
        alt="avatar"
        src={rUser}
        className={cls.avatar}
        onClick={()=>handleClick()}
      />
    </Tooltip>
  );
}

export const VEControls = props => {
  console.log("VEControls::render");
  const { dispatch } = props;
  const cls = useStyles();

  const handleAvatarClick = () => {
    console.log("handleAvatarClick");
    dispatch({type: 'setCam'});
  }

  return (
    <div className={cls.container}>
      <RandomAvatar onClick={handleAvatarClick} />
      <TextField
        id="outlined-text-input"
        label="Type in Text"
        className={cls.textField}
        margin="dense"
        variant="outlined"
        fullWidth
        multiline
        rowsMax="4"
        onChange={props.onChange}
      />
      <ChromaKeyToolbox dispatch={dispatch} />
    </div>
  );
}
