// requirement: yarn add three (^0.101.1)
// copy resources from ../public/img/
import React, {useRef, useEffect, useState} from 'react';
import * as THREE from 'three';

export const EarthMoon3D = props => {
  const rMount    = useRef(null);  // THREE mount <div>
  const rControls = useRef(null);  // to save animation start/stop function
  const rCamDir   = useRef(1);     // to save camera direction
  const [isAnimating, setAnimating] = useState(true);
  const img = fn => 'img/demo/em/' + fn;

  const init_camera_z = 1.15;

  useEffect(() => { // componentDidMount()
    let mount  = rMount.current;
    let width  = mount.clientWidth;
    let height = mount.clientHeight;
    let frameId;
    const earthVec  = new THREE.Vector3(-.5,.2,0);

    const scene     = new THREE.Scene();
    const camera    = new THREE.PerspectiveCamera(40, width/height, .01, 1000);
    camera.position.z = init_camera_z;

    let   light     = new THREE.AmbientLight(0x888888);
    scene.add(light);

    light = new THREE.DirectionalLight(0xffffff, .4);
    light.position.set(5,5,5);
    scene.add(light);
    /*
    light.castShadow = true;
    light.shadowCameraNear = .01;
    light.shadowCameraFar  = 15;
    light.shadowCameraFov  = 45;
    light.shadowMapWidth  = 1024;
    light.shadowMapHeight = 1024;
*/
    const loader    = new THREE.TextureLoader();
    const geometry  = new THREE.SphereBufferGeometry(.5, 32, 32);
    const material  = new THREE.MeshPhongMaterial({
        map         : loader.load(img('earthmap1k.jpg')),
        bumpMap     : loader.load(img('earthbump1k.jpg')),
        bumpScale   : .05,
        specularMap : loader.load(img('earthspec1k.jpg')),
        specular    : new THREE.Color('grey')
      });
    const earthMesh = new THREE.Mesh(geometry, material);
    earthMesh.rotation.x += .4;
    scene.add(earthMesh);

    const cloudsMesh = new THREE.Mesh(
      new THREE.SphereGeometry(.5 + .008, 32, 32),
      new THREE.MeshPhongMaterial({
        map         : loader.load(img('fair_clouds_4k.png')),
        side        : THREE.DoubleSide,
        opacity     : .8,
        transparent : true,
        depthWrite  : false
      })
    );
    scene.add(cloudsMesh);
    cloudsMesh.rotation.x += .6;

    const moonMesh = new THREE.Mesh(
      new THREE.SphereGeometry(.08, 16, 16),
      new THREE.MeshPhongMaterial({
        map       : loader.load(img('moonmap1k.jpg')),
        bumpMap   : loader.load(img('moonbump1k.jpg')),
        bumpScale : .01
      })
    );
    //moonMesh.position.set(.25, .8, 2);
    moonMesh.position.set(0, .12, 1.1);
    scene.add(moonMesh);
    
    const starsMesh = new THREE.Mesh(
      new THREE.SphereGeometry(90, 32, 32),
      new THREE.MeshBasicMaterial({
        map  : loader.load(img('galaxy_starfield.png')),
        side : THREE.BackSide
      })
    );
    scene.add(starsMesh);

    const renderer  = new THREE.WebGLRenderer({ antialias: true });
    renderer.setClearColor('#000000');
    renderer.setSize(width, height);

    const renderScene = () => {
      renderer.render(scene, camera);
    }

    const handleResize = () => {
      width  = mount.clientWidth;
      height = mount.clientHeight;
      renderer.setSize(width, height);
      camera.aspect = width / height;
      camera.updateProjectionMatrix();
      renderScene();
    }

    const animate = () => {
      const changeCamDirection = () => rCamDir.current *= -1;
      const camDir = rCamDir.current;
      let camPos = camera.position;
//      earthMesh.rotation.x += .002;
      earthMesh.rotation.y  += .002;
      cloudsMesh.rotation.y += .0025;
      moonMesh.rotation.y   += .001;
      camPos.x += .0005*camDir;
      camPos.y += .0001*camDir;
      camPos.z += .0001*camDir;
      if (camPos.z > 1.45 ||
          camPos.z < init_camera_z) {
        changeCamDirection();
        //camera.position.set(0,0,init_camera_z);
      }

      camera.lookAt(earthVec);

      renderScene();
      frameId = window.requestAnimationFrame(animate);
    }

    const start = () => {
      if (!frameId) {
        frameId = requestAnimationFrame(animate);
      }
    }

    const stop = () => {
      cancelAnimationFrame(frameId);
      frameId = null;
    }

    mount.appendChild(renderer.domElement);
    window.addEventListener('resize', handleResize);
    start();

    rControls.current = { start, stop };

    return () => { // componetWillUnmount()
      stop();
      window.removeEventListener('resize', handleResize);
      mount.removeChild(renderer.domElement);

      scene.remove(earthMesh);
      geometry.dispose();
      material.dispose();
    }
  }, []);  // With the second parameter, the useEffect will run only once.

  useEffect(() => {
    if (isAnimating) { rControls.current.start(); }
    else             { rControls.current.stop();  }
  }, [isAnimating]);

  return <div ref={rMount}
              style={{width: props.width, height: props.height, margin: 'auto'}}
              onClick={() => setAnimating(!isAnimating)} />
}
