import React from 'react';
import './App.scss';
import { Webcam }       from './webcam';
import { VideoEditor }  from './VideoEditor';
import { DropArea }     from './DropArea';

// Material-UI
import clsx             from 'clsx';
import { makeStyles }   from '@material-ui/core/styles';
import BottomAppBar     from './BottomAppBar';
import { LeftDrawer }   from './LeftDrawer';
//import TrailerCard from './CICard';

import Grid             from '@material-ui/core/Grid';
import Paper            from '@material-ui/core/Paper';
import Fab              from '@material-ui/core/Fab';
import AddIcon          from '@material-ui/icons/Add';
import DeleteIcon       from '@material-ui/icons/Delete';
import LayersIcon       from '@material-ui/icons/Layers';
import GridList         from '@material-ui/core/GridList';
import GridListTile     from '@material-ui/core/GridListTile';
import GridListTileBar  from '@material-ui/core/GridListTileBar';
//import ListSubheader    from '@material-ui/core/ListSubheader';
import IconButton       from '@material-ui/core/IconButton';
import InfoIcon         from '@material-ui/icons/Info';
//import tileData         from './tileData';
import Tooltip          from '@material-ui/core/Tooltip';
import Slider           from '@material-ui/core/Slider';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  canvas: {
    width: 840,
  },
  layers: {
    width: 250,
  },
  appsList: {
    width: 250,
    height: 340,
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.primary,
    margin: 6,  // marginLeft
  },
  fab: {  // floating action button
    margin: theme.spacing(1),
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  appSlider: {
    width       : '80%',
    marginTop   : theme.spacing(5),
    marginLeft  : theme.spacing(3),
    paddingBottom: theme.spacing(1),
    zIndex      : 1000,
  },
  /*
  appTitle: {
    height: 16,
    lineHeight: 16,
    fontSize: 'small',
    color: 'white',
    background: 'black',
    borderRadius: '5px 5px 0 0',
  },
  */
  gridList: {
    height: 300,
  },
  icon: {
    color: 'rgba(255,255,255,.54)',
  }
}));

function App() {
  console.log("App::render");
  const cls = useStyles();
  const refCam = React.useRef(null); // <video> camera
  const refCvs = React.useRef(null); // <canvas> for video editing
  const refLD  = React.createRef();  // <LeftDrawer> to wire with <BottomAppBar> start button

  const handleVideo = on => {
    const cam = refCam.current;
    if (on) { cam.play(); }
    else    { cam.pause(); }
  }

  return (  // autoPlay
    <div className="App">
      <div className={cls.root}>
        <Grid container spacing={2}>
          <Grid item xs={9}>
            <Paper className={clsx(cls.canvas, cls.paper)}>
              <VideoEditor ref={refCvs} refVid={refCam} />
            </Paper>
          </Grid>
          <Grid item xs={3}>
            <LayersHeader>Layers</LayersHeader>
            <Paper className={clsx(cls.layers, cls.paper)}>
              <Webcam ref={refCam}
                onPlay ={()=>{console.log("Webcam::onPlay");}}
                onPause={()=>{console.log("Webcam::onPause");}}
              />
            </Paper>
            <Paper className={clsx(cls.appsList, cls.paper)}>
              <AppsList />
            </Paper>
          </Grid>
          <Grid item xs={12}>
            <DropArea
              list
              help="~ Drag & drop backdrop image files or source video files here ~"
              urls={[
                "./img/chroma01.mp4",
                "./img/chroma02.mp4",
                "./img/back01.jpg",
                "./img/ironman.gif",
              ]}
              style={{
                border: '1px dashed #ccc',
                minHeight: '200px',
                margin: '10px'
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <p className="footer">Video Editor Prototype (C) SpacetimeQ Inc, 2019</p>
          </Grid>
        </Grid>
      </div>
      <LeftDrawer ref={refLD} show={false} />
      <BottomAppBar
        onVideo={on=>handleVideo(on)}
        onStart={()=>refLD.current.click()}
      />
    </div>
  );
}

const LayersHeader = props => {
  const cls = useStyles();

  const handleLayersClick = () => { console.log("Layers Clicked"); }
  const handleAddClick    = () => { console.log("Add Clicked"); }
  const handleDeleteClick = () => {
    const el = document.getElementsByClassName('ve-l0')[0];
    if (el) {
      el.style.backgroundImage = '';
      /* React element
      let child = el.lastElementChild;
      while (child) {
        el.removeChild(child);
        child = el.lastElementChild;
      }
      */
    }
  }

  return (
    <div>
      <Fab variant="extended" size="medium" aria-label="Title" className={cls.fab}
        onClick={handleLayersClick}>
        <LayersIcon className={cls.extendedIcon} />
        {props.children}
      </Fab>
      <Fab color="primary" size="small" aria-label="Add" className={cls.fab}
        disabled
        onClick={handleAddClick}>
        <AddIcon />
      </Fab>
      <Fab size="small" aria-label="Delete" className={cls.fab}
        onClick={handleDeleteClick}>
        <DeleteIcon />
      </Fab>
    </div>
  );
}

const AppsList = props => {
  const cls = useStyles();
  const TBD = 'TBD';
  const tileData = [
    {
      img   : 'img/app01.jpg',  // filename.ext -> filename will be used as the app id
      title : 'Earth Globe',
      sub   : 'Geospatial',
    },
    {
      img   : './img/app02.jpg',
      title : 'Planet Earth',
      sub   : 'Realistic 3D',
    },
    {
      img   : './img/app03.jpg',
      title : 'ArtMoon',
      sub   : TBD,
    },
    {
      img   : './img/app04.jpg',
      title : 'Solar System',
      sub   : TBD,
    },
    {
      img   : './img/app05.jpg',
      title : '3D Demo',
      sub   : TBD,
    },
    {
      img   : './img/app06.jpg',
      title : 'SpacetimeQ',
      sub   : TBD,
    },
  ];
  const handleAppVis = (ev, newVal) => {
    const el = document.getElementsByClassName('ve-l1')[0];
    if (el) {
      el.setAttribute("style", 'z-index: ' + newVal);
    }
  }
  // <ListSubheader component="div" className={cls.appTitle}>Apps for Demo</ListSubheader>
  return (
    <GridList cellHeight={140} className={cls.gridList}>
      <GridListTile key="Subheader" cols={2} style={{ height: '70px' }}>
        <Tooltip title="Apps Visibility">
          <Slider
            className={cls.appSlider}
            defaultValue={40}
            valueLabelDisplay="auto"
            step={10}
            marks
            min={0}
            max={100}
            onChange={handleAppVis}
          />
        </Tooltip>
      </GridListTile>
      {tileData.map(tile => (
        <GridListTile key={tile.img}>
          <img className="al-icon" src={tile.img} alt={tile.title} />
          <GridListTileBar
            title={tile.title}
            subtitle={<span>{tile.sub}</span>}
            actionIcon={
              <IconButton aria-label={`info ${tile.title}`} className={cls.icon}>
                <InfoIcon />
              </IconButton>
            }
          />
        </GridListTile>
      ))}
    </GridList>
  );
};

export default App;

