import React from 'react';
import { makeStyles }    from '@material-ui/core/styles';
import SwipeableDrawer   from '@material-ui/core/SwipeableDrawer';
import List              from '@material-ui/core/List';
import Divider           from '@material-ui/core/Divider';
import ListItem          from '@material-ui/core/ListItem';
import ListItemIcon      from '@material-ui/core/ListItemIcon';
import ListItemText      from '@material-ui/core/ListItemText';
import InboxIcon         from '@material-ui/icons/MoveToInbox';
import MailIcon          from '@material-ui/icons/Mail';
                         
import Dialog            from '@material-ui/core/Dialog';
import DialogActions     from '@material-ui/core/DialogActions';
import DialogContent     from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle       from '@material-ui/core/DialogTitle';
import Button            from '@material-ui/core/Button';

const useStyles = makeStyles({
  list: {
    width: 200,
  },
  fullList: {
    width: 'auto',
  },
});

export const LeftDrawer = React.forwardRef((props, ref) => {
  console.log("LeftDrawer::render");
  const classes = useStyles();
  const [state,    setState]    = React.useState(props.show);  // open
  const [dlgTitle, setDlgTitle] = React.useState(null);

  const toggleDrawer = open => event => {
    if (event &&
        event.type === 'keydown' &&
        (event.key === 'Tab' ||
         event.key === 'Shift'))
    {
      return;
    }
    setState(open);
  };

  const handleClick = ev => { setDlgTitle(ev.target.innerText); }

  return (
    <div>
      {dlgTitle && <MsgDialog open={true} onClose={()=>setDlgTitle(null)} title={dlgTitle}>
        SpacetimeQ Prototype
      </MsgDialog>}
      <button ref={ref} onClick={toggleDrawer(true)} style={{display: 'none'}}></button>
      <SwipeableDrawer
        open={state}
        // anchor="right", "top", "bottom"
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <div
          className={classes.list}
          role="presentation"
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {['About', 'Recent', 'Movies', 'Images'].map((text, index) => (
              <ListItem button key={text} onClick={handleClick} disabled={index > 0}>
                <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
                <ListItemText primary={text} />
              </ListItem>
            ))}
          </List>
          <Divider />
          <List>
            {['demo1', 'demo2', 'demo3'].map((text, index) => (
              <ListItem button key={text} disabled >
                <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
                <ListItemText primary={text} />
              </ListItem>
            ))}
          </List>
        </div>
      </SwipeableDrawer>
    </div>
  );
});

const MsgDialog = props => {
  console.log("MsgDialog::", props.title);
  return (
    <Dialog
      open={props.open}
      onClose={props.onClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{props.title}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {props.children}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={props.onClose} color="primary" autoFocus>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );
}
