import React, { useRef, useEffect } from 'react';
// https://webrtc.github.io/adapter/adapter-latest.js
import 'webrtc-adapter'; // include adapter for srcObject shim
import './App.scss';

export const Webcam = React.forwardRef((props, ref) => {
  console.log("Webcam::render");
  const rTitle = useRef(null);
  const constraints = {
    audio: { echoCancellation: {exact: false} },
    video: { width: 1280, height: 720, facingMode: 'user' } // 'user' or 'environment'
  };

  useEffect(() => {
    if (!('mediaDevices' in navigator &&
          'getUserMedia' in navigator.mediaDevices)) {
      alert("mediaDevices not supported!");
    }
    let videoStream = null;
    (async function() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        const videoTracks = stream.getVideoTracks();
        rTitle.current.textContent = videoTracks[0].label;
        console.log('Using video device: ' + videoTracks[0].label); // FaceTime HD Camera (Built-in)
        stream.onremovetrack = function() {
          console.log('Stream ended');
        }
        window.stream         = stream;
        ref.current.srcObject = stream;
        videoStream           = stream;
        ref.current.pause();  // pause at the start
      } catch (e) {
        console.log(e.name);
        if (typeof e !== 'undefined')
          console.error(e);
      }
    })();

    return () => {  // unsure this part is needed ... 
      if (videoStream) {
        const tracks = videoStream.getTracks();
        tracks.forEach(tr => tr.stop());
        videoStream = null;
      }
    }
  }, [ref, constraints])  // when constraints change, video should be reset
  // <video ref={ref} autoPlay muted playsInline controls onPlay={()=>handlePlay()} className="vid-fw" />
  // <video onPlay> can be used as the trigger of the streaming: How can I pass this event to <VideoEditor>?
  return (
    <div className="vid-cont">
      <video ref={ref} id="v-cam"
        autoPlay muted playsInline controls
        onPlay={props.onPlay && props.onPlay}
        onPause={props.onPause && props.onPause}
        />
      <br/>
      <label ref={rTitle}></label>
    </div>
  );
});
