import React            from 'react';
import { VEControls }   from './VEControls';
import { Layers,
         EmbedYouTube } from './Layers';
import { DropArea }     from './DropArea';
import { useAnimationFrame }  from './util-ui';
import Tooltip          from '@material-ui/core/Tooltip';
// https://webrtc.github.io/adapter/adapter-latest.js
import 'webrtc-adapter'; // include adapter for srcObject shim
import './App.scss';

const VE_SZ = {w: 800, h: 450};  // Video Editor Size

const rgb = (r, g, b) => ({r:r, g:g, b:b});

export const VideoEditor = React.forwardRef((props, rCvs) => {
  console.log("VideoEditor::render");
  let { refVid } = props;  // ref to the video input
  const initState = {
    cKMode    : 1,       // Chroma Key selection mode 0: no , 1: Color Pick, 2: Back Frame
    cKA       : [],      // chroma key color ex, rgb(45,120,100)
    cKFrame   : null,
    cKThr     : 70*70,
    fps       : 15,
    curImg    : null,    // current Backdrop file URL
    curApp    : null,    // current active app id
    curTube   : null,    // current YouTube video id
    curVideo  : null,    // current YouTube video id
  };
  const [state, setState] = React.useState(initState);
  const rTxt = React.useRef(null);  // ref to the text input element

  const dispatch = action => {
    const reducer = () => {  // return null for no state change
      const hasCK = state.cKA.length > 0;
      switch (action.type) {
        case 'setCam':
          switchVideoInput( document.getElementById('v-cam') );
          return null;
        case 'setCKMode':
          //console.log('cKMode:', action.payload);
          //return { ...state, cKMode: action.payload };
          state.cKMode = action.payload;
          return state;
        case 'addCK':  // add a chroma key or a whole frame
          if (state.cKMode === 1) {
            if (!isChromaColor(action.payload)) {
              state.cKA.push(action.payload);
            }
          } else if (state.cKMode === 2) {
            state.cKFrame = action.payload;
          }
          return { ...state };
        case 'clearCK':  // purge chroma keys
          if (state.cKMode === 1) {
            if (hasCK) {
              state.cKA = [];
              return { ...state };
            }
          } else if (state.cKMode === 2) {
            if (state.cKFrame) {
              state.cKFrame = null;
              return { ...state };
            }
          }
          return null;
        case 'dropCK':  // remove the most recently added chroma key
          if (hasCK) {
            state.cKA.splice(-1,1);
            return { ...state };
          }
          return null;
        case 'setCKThr':  // set chroma key threshould
          //return { ...state, cKThr: action.payload**2};
          state.cKThr = action.payload**2;
          return null;  // no need to re-render
        default:  // throw new Error('Unknown action type!');
          console.error('dispatch error!');
      }
    }
    const newState = reducer(action);
    if (newState) {  // check if we have to rerender
      setState(newState);
    }
  }

  const isRunning = () => refVid.current &&
                         !refVid.current.paused;
  
  // https://en.wikipedia.org/wiki/Color_difference
  // Color Metric - low-cost approximation: https://www.compuphase.com/cmetric.htm
  const isChromaColor = (c, i=0) => {  // c: rgb object
    //const ck = {r: 60, g: 139, b: 116};
    const colorDist = cK => {
      const rm = (c.r + cK.r) / 3;  // red mean
      return  (2 + rm/256)*(c.r - cK.r)**2 +
                         4*(c.g - cK.g)**2 +
        (2 + (255-rm)/256)*(c.b - cK.b)**2;
    }
    if (state.cKMode === 2 && state.cKFrame) {
      const clr = state.cKFrame.data;
      if (colorDist(rgb(clr[i], clr[i+1], clr[i+2])) < state.cKThr)
        return true;
    }
    // Need to introduce pre-sorted and binary search for performance
    for(let j=0; j < state.cKA.length; j++) {
      if (colorDist(state.cKA[j]) < state.cKThr)
        return true;
    }
    return false;
    //const w  = {r: 2,  g: 4,   b: 3};  // for weighted Euclidean distance in RGB
    //return ( w.r*(r - cK.r)**2 + w.g*(g - cK.g)**2 + w.b*(b - cK.b)**2 ) < 5000; // threshould
    //return (g > 90 && r < 150 && b < 180); //if (g > 120 && r > 120 && b > 120)
  }
    /*
  const drawOnCanvas = ctx => {
    ctx.beginPath();
    ctx.strokeStyle = "Blue";
    ctx.arc(80, 80, 50, 0, Math.PI * 2, true);
    ctx.stroke();
    ctx.beginPath();
    ctx.strokeStyle = "Red";
    ctx.rect(20, 20, 200, 100);
    ctx.stroke();
  }
  */
  /*  not working
  useEffect(() => {
    const asp = refVid.current.videoWidth / refVid.current.videoHight;
    if (asp) {
      const width = 800;
      rCvs.current.width  = width;
      rCvs.current.height = Math.trunc(width / asp);
    }
  }, [rCvs, refVid]);
  */

  useAnimationFrame(() => {
    if (isRunning()) {  // will change; also, check if the video paused or ended
      const cvs = rCvs.current;
      const ctx = cvs.getContext('2d');
      ctx.drawImage(refVid.current, 0, 0, cvs.width, cvs.height);  // camera source
      //ctx.filter = 'hue-rotate(90deg)';
      //ctx.filter = 'contrast(2)';
      // SecurityError: Failed to execute 'getImageData' on 'CanvasRenderingContext2D':
      // The canvas has been tainted by cross-origin data.
      try {
        const frame = ctx.getImageData(0, 0, cvs.width, cvs.height);
        const c = frame.data; // Uint8ClampedArray, color byte
        for (let i=0; i < c.length; i+=4) {
          if ( state.cKMode > 0 && isChromaColor(rgb(c[i],c[i+1],c[i+2]), i) )
            c[i+3] = 0;  // transparency: opacity 0..255
        }
        //ctx.filter = 'drop-shadow(0 0 2px darkgray)';
        //ctx.filter = 'blur(2px)';
        //ctx.filter = 'brightness(1.5)';
        ctx.putImageData(frame, 0, 0);
      }
      catch (e) {  // tainted
      }
    }
  }, 1000/state.fps);

  /*
  React.useEffect(() => {
    let id = null;
    const tick = () => {
      if (isRunning()) {  // will change; also, check if the video paused or ended
        const cvs = rCvs.current;
        const ctx = cvs.getContext('2d');
        ctx.drawImage(refVid.current, 0, 0, cvs.width, cvs.height);  // camera source
        //ctx.filter = 'brightness(2)';
        const frame = ctx.getImageData(0, 0, cvs.width, cvs.height);
        const c = frame.data; // Uint8ClampedArray, color byte
        for (let i=0; i < c.length; i+=4) {
          if ( state.cKMode > 0 && isChromaColor(rgb(c[i],c[i+1],c[i+2]), i) )
            c[i+3] = 0;  // transparency: opacity 0..255
        }
        ctx.putImageData(frame, 0, 0);
        id = setTimeout(tick, 1000/state.fps);  // milliseconds/fps
      } else {
        //console.log("No video!");
        id = setTimeout(tick, 1000/4);  // check period
      }
    }
    tick();  // actually this should be triggered by onPlay from <video>
    if (id)
      return () => clearTimeout(id);
    // react-hooks/exhaustive-deps; to ignore isChromaKey, fps (updates by the timer)
    // eslint-disable-next-line
  }, [refVid, state]); // state should be included even though it is a global variable

  */
  // const rgb2hex = (r, g, b) => ( (r << 16) | (g << 8) | b ).toString(16);  // no range check

  const handleClick = ev => {
    //console.log("handleClick", ev);
    ev.preventDefault();
    ev.stopPropagation();  // not to fire twice
    //console.log(refVid, refVid.current.paused);
    if (!isRunning() || state.CKMode === 0)
      return;
    try {
      const cvs = rCvs.current;
      const ctx = cvs.getContext('2d');
      if (state.cKMode === 1) {  // Pick a chorma key color
        const x = ev.pageX - cvs.offsetLeft;
        const y = ev.pageY - cvs.offsetTop;
        const c = ctx.getImageData(x, y, 1, 1).data;
        console.log(x, y, c);
        if (!c[3])  // transparent pixel (existing chroma-key)
          return;
        dispatch({type: 'addCK', payload: rgb(c[0],c[1],c[2])});
        //const h = "#" + ("000000" + rgb2hex(c[0], c[1], c[2])).slice(-6);
      } else if (state.cKMode === 2) {  // chroma key Frame
        ctx.drawImage(refVid.current, 0, 0, cvs.width, cvs.height);  // camera source
        console.log("Chroma Key Frame captured");
        dispatch({type: 'addCK', payload: ctx.getImageData(0, 0, cvs.width, cvs.height)});
      }
    }
    catch (e) {
    }
  }

  const handleTextInput = ev => {
    const len = ev.target.value.length;
    const n_lines = Math.trunc(len * 30 / 800);
    rTxt.current.style.transform = (n_lines >= 3) ? `translateY(${-n_lines*20}px)` : "";
    rTxt.current.textContent = ev.target.value;
  }

  const switchVideoInput = vEl => {  // video element
    if (vEl) {  // pause current video (or webcam) and play new video
      refVid.current.pause();
      refVid.current = vEl;
      vEl.play();
    }
  }

  const handleDropFileInfo = fInfo => {
    console.log("handleDropFileInfo::", fInfo);
    if (fInfo.type.match('^video/')) {
      if (fInfo.url.match('^http') &&
        !fInfo.url.includes(window.location.hostname)) {
        if (state.curVideo !== fInfo.url) {
          setState({...state, curVideo: fInfo.url});
        }
      } else {
        switchVideoInput( document.getElementById(fInfo.id) );
      }
    } else if (fInfo.type.match('^image/')) {
      if (state.curImg !== fInfo.url) {
        setState({...state, curImg: fInfo.url});
      }
    } else if (fInfo.type.match('^app')) {
      if (state.curApp !== fInfo.id) {
        setState({...state, curApp: fInfo.id});
      }
    } else if (fInfo.type.match('^youtube')) {
      if (state.curTube !== fInfo.id) {
        setState({...state, curTube: fInfo.id});
      }
    }
  }

  const CKPalette = () => {  // Chroma-Keys Palette
    const RGBstr = c => `rgb(${c.r},${c.g},${c.b})`;
    const CKColor = ({color}) => <Tooltip title={color}><span style={{color: color}}>█</span></Tooltip>;
    //console.log(state.cKA);
    return (
      <div className="ckpalette">
        chroma keys:&nbsp;
        {state.cKA.map((c, i) => <CKColor key={i} color={RGBstr(c)} />)}
      </div>
    );
  }

  React.useEffect(() => {
    const delay = 200;
    const msg = "SpacetimeQ Web Video Editor Prototype ...";
    let i = 0;
    let elapsed = 0;

    function tick() {
      const txt = rTxt.current;
      if (!txt)
        return;
      elapsed += delay;
      if(i < msg.length) {
        txt.textContent += msg[i++];
      } else {
        txt.style.opacity = 0;  // transition required
      }
      if (elapsed > 16*1000) {
        clearInterval(id);
        txt.textContent   = '';
        txt.style.opacity = 1;
      }
    }
    let id = setInterval(tick, delay);
    return () => clearInterval(id);
  }, []);

  return (
    <>
      <Layers width={VE_SZ.w} height={VE_SZ.h} appId={state.curApp}>
        <div className="ve-l0"
          style={state.curImg ? {backgroundImage: `url(${state.curImg})`} : null}
        >
          {state.curTube &&
            <EmbedYouTube width={VE_SZ.w} height={VE_SZ.h} vID={state.curTube} />} 
          {state.curVideo &&
            <video width={VE_SZ.w} height={VE_SZ.h}
                   loop controls playsInline autoPlay src={state.curVideo} />}
        </div>
        <DropArea noUi
          onDropFileInfo={handleDropFileInfo}
        >
          <canvas ref={rCvs} id="v-ed"
            width={VE_SZ.w} height={VE_SZ.h} onClick={handleClick}
          />
          <p ref={rTxt} className="text-in"></p>
        </DropArea>
      </Layers>
      <VEControls dispatch={dispatch} onChange={handleTextInput} />
      <CKPalette />
    </>
  );
});
